import { useNavigate } from "react-router-dom";
import { Row, Col, Card, Button, Typography } from "antd";

const { Text } = Typography;

function TravelCard({ travelInfo }) {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(`/travel/${travelInfo.id}`);
  };

  return (
    <>
      <Card
        hoverable
        cover={
          <div style={{ position: "relative", width: "100%" }}>
            <img
              alt="product"
              src={"http://127.0.0.1:8000" + travelInfo.cover_image}
              style={{ width: "100%", aspectRatio: "16/9", objectFit: "cover" }}
            />
            {/* <div
              style={{
                position: "absolute",
                top: '0px', 
                left: '0px', 
                transform: "rotate(-45deg)",
                backgroundColor: "green",
              }}
            >
              <Text tt type="danger">
                热门
              </Text>
            </div> */}
          </div>
        }
        actions={[
          <div
            style={{
              display: "flex",
              justifyContent: "space-around",
              alignItems: "center",
              width: "100%",
            }}
          >
            <div>
              {/* 显示价格的部分 */}
              <p style={{ fontSize: 18, fontWeight: "bold" }}>¥ 100.00</p>
            </div>
            <Button type="primary" key="order" onClick={handleClick}>
              详情
            </Button>
          </div>,
        ]}
        // style={{width:"100%"}}
      >
        {travelInfo.title}
      </Card>
    </>
  );
}

export default function TravelCards({ travelList }) {
  return (
    <>
      <Row gutter={[0, 8]} justify="center" style={{width:"100%"}}>
        {/* gutter属性用于设置每个列之间的间隔 */}
        {travelList.map((item) => (
          <Col key={item.id} style={{width:"100%"}}>
            <TravelCard travelInfo={item} />
          </Col>
        ))}
      </Row>
    </>
  );
}
